import React, { Fragment } from "react";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import { Link } from "react-router-dom";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import PageComponent from "@Components/core/PageComponent";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import style from "../styles.css";
import HotLink from "@Components/layouts/common/HotLink/HotLink";
import servicesName from "../services.constant";
import RemarketingSVG from "./RemarketingSVG";

export default class Remarketing extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configuro landscape --> */}
        <div id="lying" />

        {/* <!-- Configuro natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside remarketing">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Remarketing</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dgitozero</Link>
                  </li>
                  <li>
                    <Link to="/ads">Ads</Link>
                  </li>
                  <li>
                    <span>Remarketing</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <RemarketingSVG />
                    <h2 style={style.mainTitle}>Acerte o alvo</h2>
                    <p style={style.subTitle}>
                      Reimpacte automaticamente os usurios
                    </p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>O que  o Remarketing?</h3>
                        <p style={style.p}>
                           conhecida por Remarketing a ao de reimpactar
                          automaticamente usurios que visitaram previamente seu
                          site ou clicaram em algum link seu, por exemplo. Ou
                          seja,  uma ao que ajuda no fortalecimento da
                          presena da sua marca online perante aqueles que j
                          demonstraram interesse em saber mais sobre a sua
                          empresa, produto ou servio, instigando-os a lembrar
                          de voc e despertar novamente a vontade de comprar.
                        </p>
                        <h3 style={style.h3}>Remarketing no Adwords</h3>
                        <p style={style.p}>
                          Para conquistar melhores resultados sua empresa
                          precisa utilizar todos os mecanismos para atingir os
                          seus clientes. podemos criar campanhas de Remarketing
                          no <HotLink to="/ads/google-adwords">Google Adwords</HotLink> aumentando a exposio da sua marca
                          junto ao seu cliente. Com esta estratgia, voc poder
                          desfrutar de mais vendas e converses, alcanando as
                          suas metas internas e crescendo a sua empresa.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>Remarketing no Facebook</h3>
                        <p style={style.p}>
                          Imagine que seu potencial cliente viu seu produto em
                          seu site, mas resolveu pensar melhor antes de comprar.
                          Horas depois este mesmo usurio resolve se distrair no
                          Facebook. Ao invs de perder este potencial cliente, o
                          remarketing o far segui-lo criando anncios dentro
                          do Facebook que possam chamar sua ateno novamente e,
                          assim, lev-lo a converter. Se, em um outro exemplo,
                          voc vende cursos e um usurio visitou seu site
                          pesquisando sobre os seus cursos, mas no converteu e
                          foi conversar com os amigos no Facebook. Seu site j
                          foi fechado e esquecido, mas o remarketing continua
                          trabalhando seguindo o usurio e exibindo publicidade
                          relevante a ele no Facebook, mais precisamente sobre
                          aquele curso que ele passou horas pesquisando em seu
                          site. Com estratgias segmentadas o Remarketing no
                          Facebook pode reconquistar aquele usurio que seu site
                          considerava perdido em poucos minutos.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.INBOUND} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



