import React, { Fragment } from "react";
import Breadcrumb from "@Components/layouts/common/Breadcrumb/Breadcrumb";
import BannerTitle from "@Components/layouts/common/BannerTitle/BannerTitle";
import NavBar from "@Components/layouts/common/NavBar/NavBar";
import { Link } from "react-router-dom";
import MapaSite from "@Components/layouts/common/MapaSite/MapaSite";
import RedesSociais from "@Components/layouts/common/RedesSociais/RedesSociais";
import Footer from "@Components/layouts/common/Footer/Footer";
import PageComponent from "@Components/core/PageComponent";
import FormOrcamentoServico from "@Components/forms/common/FormOrcamentoServico";
import style from "../styles.css";
import servicesName from "../services.constant";
import GoogleAdwordsSVG from "./GoogleAdwordsSVG";

export default class GoogleAdwords extends PageComponent {
  render() {
    return (
      <Fragment>
        {/* <!-- Configuro landscape --> */}
        <div id="lying" />

        {/* <!-- Configuro natural --> */}
        <div id="standing">
          <div id="wrapper-main" className="page-inside google-adwords">
            <NavBar />

            <main>
              <section id="wrapper-banner">
                <BannerTitle>
                  <h1>
                    <strong>Google Adwords</strong>
                  </h1>
                </BannerTitle>
              </section>

              <section id="wrapper-master">
                <Breadcrumb style={style.breadcrumb}>
                  <li>
                    <Link to="/">dgitozero</Link>
                  </li>
                  <li>
                    <Link to="/ads">Ads</Link>
                  </li>
                  <li>
                    <span>Google Adwords</span>
                  </li>
                </Breadcrumb>

                <div style={style.background}>
                  <div className="container" style={style.subContainer}>
                    <GoogleAdwordsSVG />
                    <h2 style={style.mainTitle}>
                      O servio de publicidade online
                    </h2>
                    <p style={style.subTitle}>
                      Consultoria de Links Patrocinados
                    </p>
                    <span style={style.hr} />
                    <div className="row">
                      <div className="col-xl-5 col-lg-5 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>O que  o Google Adwords</h3>
                        <p style={style.p}>
                          Google Adwords  a plataforma atravs da qual so
                          feitos anncios pagos no Google, chamados de links
                          patrocinados. Eles so a melhor forma de conseguir
                          exposio rpida e atrair cliques para seu site, pois
                          te colocam no topo das pginas de resultados de
                          pesquisa em posio de destaque. Os links patrocinados
                          tambm so comumente usados para anunciar os
                          principais produtos das empresas, alm de promoes e
                          outros tipos de campanha de tempo limitado.
                        </p>
                        <h3 style={style.h3}>
                          Como o Google Adwords funciona?
                        </h3>
                        <p style={style.p}>
                          O Google Adwords  o principal servio de publicidade
                          online do Google e responsvel por grande parte da sua
                          receita. Os servios do Adwords so populares entre
                          empresas do mundo todo e largamente utilizados por
                          aquelas que tem uma estratgia de Inbound Marketing
                          integrada, pois o Google Adwords  uma das formas mais
                          rpidas e eficazes de atrair trfego para o seu site,
                          app ou blog. Qualquer pessoa ou empresa pode utilizar
                          o Google Ads, basta criar uma conta no site e
                          cadastrar um meio de pagamento, onde o carto de
                          crdito  o meio mais comumente escolhido.
                        </p>
                      </div>
                      <div className="col-xl-5 offset-xl-2 col-lg-5 offset-lg-2 col-md-12 col-sm-12 col-12">
                        <h3 style={style.h3}>
                          Os servios so cobrados atravs de 2 mtricas
                          distintas: CPM ou CPC.
                        </h3>
                        <p style={style.p}>
                          CPM  o Custo por Mil Impresses, ou seja, voc 
                          cobrado a cada mil visualizaes do seu anncio.  a
                          mtrica mais indicada se o objetivo de Marketing for
                          divulgar um produto ou servio, para que se tenha
                          ideia de quantas pessoas a propaganda impactou. CPC 
                          o Custo Por Clique, ou seja, o valor  cobrado apenas
                          se a pessoa clicar no seu anncio.  uma mtrica
                          bastante utilizada por empresas que querem aumentar o
                          nmero de visitantes no site e adquirir mais leads que
                          resultem em novas oportunidades de negcio.
                        </p>
                        <h3 style={style.h3}>
                          Como a dgitozero pode te ajudar?
                        </h3>
                        <p style={style.p}>
                          Fazemos uma Consultoria de Links Patrocinados em que
                          conduzimos o cliente desde o Planejamento da Campanha
                          at a mensurao e otimizao de resultados. Cada
                          cliente tem um caso diferente, ento aqui na
                          dgitozero personalizamos cada projeto com as
                          especificidades necessrias.
                        </p>
                      </div>
                    </div>
                  </div>
                </div>
                <FormOrcamentoServico service={servicesName.GOOGLE_ADWORDS} />
              </section>

              <section id="wrapper-map" className="featured">
                <MapaSite />
              </section>

              <section id="wrapper-social-networks">
                <RedesSociais />
              </section>
            </main>

            <footer id="wrapper-footer">
              <Footer />
            </footer>
          </div>
        </div>
      </Fragment>
    );
  }
}



